@import '../../assets/scss/com.scss';
.mero{
    width: 100%;
    height: 100%;
    background-color:$pageBackColor;
    .mero_con{
        width: 100%;
        margin-top: 20px;
        padding: 0 30px;
        box-sizing: border-box;
        background-color: #fff;
        .con_box{
            width: 100%;
            height: 143px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-bottom: 1px solid #B7B7B7;
            .box_left{
                .left_pullIn2{
                    width: 116px;
                    .pullIn2_top{
                        width: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: flex-end;
                        img{
                            width: 18px;
                            height: 18px;
                        }
                    }
                    .pullIn2_text{
                         width:100%;
                         font-size: 28px;
                         font-family: Microsoft YaHei;
                         font-weight: 400;
                         color: #363636;
                    }
                    .pullIn2_fl{
                         font-size: 26px;
                         font-family: Source Han Sans CN;
                         font-weight: 400;
                         color: #8E8E8E;
                         white-space:nowrap;
                    }
                }
                .left_pullin3{
                    height: 92px;
                    font-size: 36px;
                    font-family: Microsoft YaHei;
                    font-weight: bold;
                    color: $myBackColor;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
            .box_right{
                height: 92px;
                display: flex;
                justify-content: center;
                align-items: center;
                .right_text{
                    width: 140px;
                    height: 35px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background: #FFFFFF;
                    border: 1px solid $myBackColor;
                    border-radius: 10px;
                    font-size: 20px;
                    font-family: Microsoft YaHei;
                    font-weight: 400;
                    color: $myBackColor;
                }
            }
        }
    }
}